import React from "react";
import RecordRTC from 'recordrtc';
import { config } from './config';

class Recorder extends React.Component {
  state = {
    initial: true,
    file: null,
    showSelect: false
  };

  a = () =>  {
    const setState = config => this.setState(config);
    if (navigator.mediaDevices === undefined) {
      alert("이 브라우저는 지원하지 않습니다. 다른 브라우저를 이용해 주세요.");
      return;
    }
    navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true
    }).then(async function(stream){
      document.getElementById("video").srcObject = stream;
      document.getElementById("video").play();
      setState({stream: stream});
    }); 
  }

  onStart = () => {
    const {stream} = this.state;
    let recorder = RecordRTC(stream, config);
    recorder.startRecording();
    this.setState({status: "recording..", recorder: recorder});
  };

  onFinish = () => {
    const setState = config => this.setState(config);
    const {recorder} = this.state;
    recorder.stopRecording(function() {
      const blob = recorder.getBlob();
      var link = document.createElement("a"); // Or maybe get it from the current document
      link.href = URL.createObjectURL(blob);
      link.download = "aDefaultFileName.webm";
      link.innerHTML = "Click here to download the file";
      document.body.appendChild(link); // Or append it wh
      setState({status: "done"});
    });
  };

  onClickRecord = () => {
    const {audioOnly} = this.props;
    const setState = config => this.setState(config);
    // 마이크와 영상 권한을 얻는다.
    this.setState({initial: false});
    navigator.mediaDevices.getUserMedia(
      audioOnly ? { audio: true } : { video: true, audio: true }
    ).then(async function(stream){
      const video = document.getElementById("video");
      video.srcObject = stream;
      video.muted = true;
      video.play();
    }).catch(e => {
      alert("이 브라우저는 실시간 녹음 또는 녹화가 지원되지 않습니다. 다른 브라우저를 이용하거나 파일 업로드 기능을 이용해 주세요.");
      setState({initial: true});
    }); 
  };

  onChangeFile = v => {
    const file = v.target.files[0];
    // initial 상태를 풀고, 비디오에 영상을 넣는다.
    this.setState({file, initial: false}, () => {
      console.log(file);
      document.getElementById("video").src = URL.createObjectURL(file);
    });
  };

  render () {
    const {initial} = this.state;
    const {audioOnly} = this.props;
    return (
      <div style={{
        width: 600,
        height: 520, 
        display: "flex", 
        flexDirection: "column", 
        alignItems: "center",
        justifyContent: "space-between",
        padding: 20,
        backgroundColor: "#ddd"
      }}>
        {initial ? (
          <div style={{
            width: 600, height: 400, display: "flex", flexDirection: "column", 
            alignItems: "center", justifyContent: "center", backgroundColor: "white"}}>
            <button onClick={this.onClickRecord} >실시간 {audioOnly ? "녹음" : "녹화"}</button>
            <input type="file" accept={audioOnly ? "audio/*" : "video/*"} onChange={this.onChangeFile} />
            <span>위 버튼을 누르면 <b>영상 촬영</b>을 할 수 있습니다.</span>
            <span>녹음/녹화를 마치신 후 <b>확인</b> 버튼을 눌러주세요.</span>
          </div>
        ) : (
          <video id="video" controls width={600} height={480} style={{backgroundColor: "transparent"}} />
        )}
        <div style={{display: "flex", flexDirection: "row"}}>
          <button onClick={this.onClickCancel} >취소</button>
        </div>
      </div>
    );
  }
}

export default Recorder;